/* eslint-disable */
import React, { useEffect, useState } from "react";
import { useNavigate, useParams } from 'react-router-dom';
import './Detail.scss'
import styled from 'styled-components';

let 박스 = styled.div`
    padding: 20px;
`;
let 제목 = styled.h4`
    font-size: 25px;
    color: ${ props => props.색상 }
`;

function Detail(props){
    
    let [상태, 상태변화] = useState(true);
    let [인풋상태, 인풋상태변화] = useState('');

    function onChange(e){
        인풋상태변화(e.target.value);
        console.log(인풋상태);
    }

    useEffect(()=>{
        let 타이머 = setTimeout(()=>{상태변화(false);},2000)
        return ()=>{clearTimeout(타이머)}
    },[상태]);

    let {id} = useParams();
    let 찾은상품 = props.shoes.find(function(상품){
        return 상품.id == id
    });
    let navigate = useNavigate();

    return (
        <div className="container">
            <박스>
                <제목 색상='#888' className="red">Detail</제목>
            </박스>

            {인풋상태}
            <input onChange={onChange}/>

            {
                상태 === true 
                ? (<div className="my-alert">
                    <p>재고가 얼마 남지 않았습니다.</p>
                  </div>)
                : null
            }
            <div className="row">
            <div className="col-md-6">
                <img src={`https://codingapple1.github.io/shop/shoes1.jpg`} width="100%" />
            </div>
            <div className="col-md-6 mt-4">
                <h4 className="pt-5">{찾은상품.title}</h4>
                <p>{찾은상품.content}</p>
                <p>{찾은상품.price}원</p>
                <Info 재고={props.재고}/>
                <button className="btn btn-danger" onClick={()=>{props.재고변경([9,11,12])}}>주문하기</button> 
                <button className="btn btn-danger" onClick={()=>{
                    navigate(-1);
                }}>뒤로가기</button> 
            </div>
            </div>
        </div> 
    );
  }

function Info(props){
    return(
        <p>재고 : {props.재고[0]}</p>
    );
}

export default Detail;