import React, { useEffect, useState } from 'react';
import Shoe from './Shoe';
import './Mainpage.css'
import { Button } from 'react-bootstrap'
import axios from 'axios';

function Mainpage(props) {

    let [버튼상태, 버튼상태변경] = useState(true);
    
    useEffect(()=>{
        axios.get('https://codingapple1.github.io/shop/data2.json')
            .then((result)=>{
                result.data[result.data.length-1].id == props.shoes[props.shoes.length-1].id
                ? 버튼상태변경(false)
                : 버튼상태변경(true)
            })
            .catch(()=>{
                console.log('실패했어요')
            })
    },[props.shoes])

    return (
        <div>
            {/* 메인이미지 */}
            <div className='background'>
                <h1>20% Season Off</h1>
                <p className='text-box'>
                This is a simple hero unit, a simple jumbotron-style component for calling
                extra attention to featured content or information.
                </p>
                <p>
                <Button variant="primary">Show more</Button>
                </p>
            </div>
            {/* 상품리스트 */}
            <div className='container'>
                <div className='row'>
                    {
                    props.shoes.map((item, i)=>(
                        <Shoe item={item} i={i} key={i}/>
                    ))
                    }
                </div>
            </div>
            {
                버튼상태 == true
                ? (
                    <button className='btn btn-primary' onClick={()=>{
                        axios.get('https://codingapple1.github.io/shop/data2.json')
                        .then((result)=>{
                            props.shoes변경([...props.shoes, ...result.data])  
                            console.log(result.data[result.data.length-1].id)
                            console.log(props.shoes[props.shoes.length-1].id)
                        })
                        .catch(()=>{
                            console.log('실패했어요')
                        })
        
                    }}>더보기</button>
                )
                : null
            }
            
        </div>
    );
}
export default Mainpage;